

#options-section {
  z-index: 3;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 99%;
  --selected-color: rgb(190, 199, 216);
  --hover-color: rgb(206, 206, 206);
}

button {
  color: rgb(255, 255, 255);
  font-size: 15px;
  font-weight: 600;
  font-family: serif;
}

.Selected {
  background: var(--selected-color);
}

.Unselected {
  background: rgba(0, 0, 0, 0.5);
}

.Selected:hover{
  background-color: var(--hover-color);
}
.Unselected:hover{
  background-color: var(--hover-color);
}

/*ABOVE Shared settings with rest of this section components*/

#option-buttons-container {
  display: grid;
  grid-template-columns: 90px 90px 90px 90px 90px calc(100% - 450px);
  padding: 2px;
  z-index: 9999 !important;
}
#option-buttons-container >button {
  margin-left: 5px;
  border-radius: 20px;
  width: 85px;
  height: 6vh;
}
#option-buttons-container >p {
  margin-block-start: 0px;
  margin-block-end: 0px; 
}